export default {
	data() {
		return {
			baseUrl:this.$common.baseUrl,
			langch:{
				yonghu:'用户设置',
				avatar:'修改头像',
				username:'名称',
				sex:'性别',
				age:'年龄',
			},
			langen:{
				yonghu:'setUserinfo',
				avatar:'avatar',
				username:'username',
				sex:'sex',
				age:'age',
			},
			langtype:'',
			avatar:uni.getStorageSync('userinfo').avatar,
			username:uni.getStorageSync('userinfo').username,
			// 接收传过来的修改个人信息
			myobj:{},
			myimg:'',
			name:'',
			sex:'',
			age:'',
			list:'',
			urls:this.$common.baseUrl,
		}
	},
	onShow() {
		this.langtype = uni.getStorageSync('lang')?uni.getStorageSync('lang'):'CH'
	},
	methods: {
		// bluetooth(){
		// 	uni.navigateTo({
		// 		url:'../bluetooth/bluetooth'
		// 	})
		// },
		// 点击修改头像
			//上传图片
			onTopPic() {
				this.$common.upimg().then(res => {
					this.avatar = res[0];
					let obj = {
						avatar:this.avatar
					}
					var that = this	
					if(this.avatar){
						this.$common.requests('/api/user/profile',obj).then(res=>{
							// 成功后重新请求数据
							if(res.data.code == 1){
								// console.log(5555555)
								that.$_myinit()
							}
							
						})
					}
				})	
			},
		// 修改用户信息
		saveprofile(){
			var language = null
			if(this.langtype == 'CH'){
				language = 1
			}else{
				language = 2
			}
			var that = this	
			let obj = {
				nickname:this.name,
				gender:this.sex,
				age:this.age,
				language:language
			}
			this.$common.requests('/api/user/profile',obj).then(res=>{
				// 成功后重新请求数据
				if(res.data.code == 1){
					that.$_myinit()
				}
				
			})
		},
		back(){
			 uni.navigateBack({
				delta:1
			 })
		},
	}
}
